import React, { useEffect } from 'react';
import { loadUserData } from '../../actions';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';

import createHashHistory from '../../history';
import Sidebar from '../Sidebar/Sidebar';
import Messages from '../Messages/Messages';
import SendMessage from '../SendMessage/SendMessage';
import Header from '../Header/Header';

export default function Dashboard() {

  // Get State from Redux Store
  const user = useSelector(state => state.user);
  const dispatch = useDispatch();

  // Listens for changes on isSignedIn
  // Gets initial user data upon change
  useEffect(() => {
    if (!user.isSignedIn) {
      createHashHistory.push('/');
    }
    else
      dispatch(loadUserData(user.userId));

  }, [dispatch, user.isSignedIn, user.userId])

  useEffect(() => {
    requestFullScreenMobile();
  });

  const requestFullScreenMobile = () => {
    if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
      if (navigator.userAgent !== "Mozilla/5.0 (Linux; Android 6.0; Nexus 5 Build/MRA58N) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/76.0.3809.100 Mobile Safari/537.36")
        document.documentElement.requestFullscreen();
    }
  }


  return (
    <div className="grid-container">

      <div className="sidebar-grid">
        <Sidebar />
      </div>

      <div className="messages-grid">
        <Header />
        <Messages />
      </div>

      <div className="send-messages-grid">
        <SendMessage />
      </div>

    </div >
  )
}
